@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700);
body {
  margin: 0;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 960px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.utility-label p {
  margin: 10px 0 10px;
}

.utility-label input {
  padding: 5px;
  font-size: 16px;
  width: 300px;
  border-radius: 4px;
}

.utility-label textarea {
  padding: 5px;
  font-size: 16px;
  width: 400px;
  height: 100px;
  border-radius: 4px;
}

.utility-component-block {
  border-bottom: 1px solid #666;
  padding-bottom: 10px;
}

.utility-component-block h2 {
  text-decoration: underline;
}

.ComponentBlockSelector {
  margin-top: 15px;
  display: flex;
}

.ComponentBlockSelector-select-holder {
  width: 300px;
}

.ComponentBlockSelector button {
  margin-left: 10px;
  background: #308dc6;
  color: #fff;
  font-size: 16px;
  border-radius: 4px;
  padding: 0 15px;
}

.ComponentBlockSelector-header {
  margin: 10px 0;
}

.TitleDescription {
  display: flex;
  align-items: center;
  justify-content: center;
}

.TitleDescription > div {
  margin: 0 40px; 
}

.SaveBar {
  display: flex;
  background: #0085ca;
  color: #fff;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px 15px;
}

.SaveBar-header {
  font-size: 26px;
  margin: 0;
}

.SaveBar-button-container {
  margin-left: auto;
  display: flex;
}

.SaveBar-button-container div {
  margin-left: 15px;
}

.SaveBar-button-container button {
  border: 1px solid #fff;
  background: #ff6900;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  padding: 5px 10px;
  cursor: pointer;
  -webkit-transition: .25s;
  transition: .25s;
}

.SaveBar-button-container button:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  background: #fff;
  color: #ff6900;
}

.SaveBar-button-container button:active {
  -webkit-transform: translateY(0px) scale(.9);
          transform: translateY(0px) scale(.9);
}

.SaveBar-button-container button:first-child {
  border-radius: 5px 0 0 5px;
}

.SaveBar-button-container button:nth-child(2) {
  border-radius: 0 5px 5px 0;
}

.MainLayout-container {
  display: flex;
}

.MainLayout-container > div {
  padding: 10px;
  width: 50%;
}

